</div></demo-block>
<h2>API</h2>
<h4>QiuSwitch属性</h4>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>状态值</td>
<td>Boolean</td>
<td>-—</td>
<td>false</td>
</tr>
<tr>
<td>size</td>
<td>尺寸</td>
<td>String</td>
<td>small, normal, large</td>
<td>normal</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用状态</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h4>QiuSwitch Slots</h4>
<table class="table"><thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>checkedText</td>
<td>自定义打开时的内容</td>
</tr>
<tr>
<td>unCheckedText</td>
<td>自定义关闭时的内容</td>
</tr>
</tbody>
</table>
<h4>QiuSwitch 事件</h4>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>开关状态变化时触发</td>
<td>开关的状态</td>
</tr>
</tbody>
</table>
</section></template>

<script>
    export default {
        data () {
            return {
                value: ''
            }
        }
    }
</script>


